import { request } from 'utils';

const prefix = '/api/categories';


export const getStudyFieldGroups = ({
  label,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/StudyFieldGroup',
    method: 'GET',
    params: {
      label,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });



